"use client";
import React, { useState } from 'react';

interface FormModalProps {
  open: boolean;
  onClose: () => void;
  onConfirm: (data: {
    fullName: string;
    address: string;
    mobile: string;
    email: string;
    gender: 'male' | 'female';
  }) => void;
}

const FormModal: React.FC<FormModalProps> = ({ open, onClose, onConfirm }) => {
  const [fullName, setFullName] = useState('');
  const [address, setAddress] = useState('');
  const [mobile, setMobile] = useState('');
  const [email, setEmail] = useState('');
  const [gender, setGender] = useState<'male' | 'female'>('male');

  if (!open) return null;

  return (
    <div className="fixed inset-0 z-50 flex items-center justify-center bg-black/50">
      <div className="bg-white rounded-2xl shadow-2xl p-8 w-full max-w-md relative">
        <button onClick={onClose} className="absolute top-3 right-3 text-gray-400 hover:text-gray-700 text-2xl font-bold">&times;</button>
        <h2 className="text-2xl font-bold mb-6 text-center text-[#943FC9]">Order Pi Box</h2>
        <form
          onSubmit={e => {
            e.preventDefault();
            onConfirm({ fullName, address, mobile, email, gender });
          }}
          className="space-y-4"
        >
          <div>
            <label className="block text-gray-700 font-medium mb-1">Full Name</label>
            <input type="text" required value={fullName} onChange={e => setFullName(e.target.value)} className="w-full px-4 py-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-[#943FC9]" />
          </div>
          <div>
            <label className="block text-gray-700 font-medium mb-1">Address</label>
            <textarea required value={address} onChange={e => setAddress(e.target.value)} className="w-full px-4 py-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-[#943FC9] resize-none" rows={2} />
          </div>
          <div className="flex gap-4">
            <div className="flex-1">
              <label className="block text-gray-700 font-medium mb-1">Mobile No</label>
              <input type="tel" required value={mobile} onChange={e => setMobile(e.target.value)} className="w-full px-4 py-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-[#943FC9]" />
            </div>
            <div className="flex-1">
              <label className="block text-gray-700 font-medium mb-1">Email ID</label>
              <input type="email" required value={email} onChange={e => setEmail(e.target.value)} className="w-full px-4 py-2 border rounded-lg focus:outline-none focus:ring-2 focus:ring-[#943FC9]" />
            </div>
          </div>
          <div>
            <label className="block text-gray-700 font-medium mb-1 mb-2">Gender</label>
            <div className="flex gap-6">
              <label className="flex items-center gap-2">
                <input type="radio" name="gender" value="male" checked={gender === 'male'} onChange={() => setGender('male')} className="accent-[#943FC9]" />
                <span>Male</span>
              </label>
              <label className="flex items-center gap-2">
                <input type="radio" name="gender" value="female" checked={gender === 'female'} onChange={() => setGender('female')} className="accent-[#943FC9]" />
                <span>Female</span>
              </label>
            </div>
          </div>
          <button type="submit" className="w-full bg-[#943FC9] text-white py-3 rounded-lg font-semibold text-lg hover:bg-[#7a2fa0] transition-colors">Confirm</button>
        </form>
      </div>
    </div>
  );
};

export default FormModal; 